@import "https://fonts.googleapis.com/css?family=Inter";
.aqua-button {
  -ms-appearance: none;
  appearance: none;
  vertical-align: middle;
  color: inherit;
  font: inherit;
  text-align: inherit;
  text-transform: inherit;
  -webkit-tap-highlight-color: transparent;
  cursor: pointer;
  color: #fff;
  text-shadow: 0 1px 1px #115be7;
  background-color: #570909;
  background-image: radial-gradient(75% 50% at 50% 0, #f4feff, rgba(0, 0, 0, 0)), radial-gradient(75% 35% at 50% 80%, #260209, rgba(0, 0, 0, 0));
  background-position: 0 0;
  background-repeat: repeat;
  background-size: auto;
  background-attachment: scroll;
  background-origin: padding-box;
  background-clip: border-box;
  border: 1px solid #150808;
  border-radius: 50px;
  margin: 3px;
  padding: 12px 16px;
  transition-property: border-color, transform, background-color;
  transition-duration: .2s;
  position: relative;
  box-shadow: inset 0 -2px 4px 1px rgba(0, 0, 0, .6), inset 0 -4px 4px 1px #201112, inset 0 0 2px 1px rgba(255, 255, 255, .2), 0 1px 4px 1px rgba(32, 25, 25, .2), 0 1px 4px 1px rgba(0, 0, 0, .1);
}

.aqua-button:after {
  content: "";
  width: 80%;
  height: 40%;
  opacity: .75;
  background-image: linear-gradient(#f4feff, rgba(0, 0, 0, 0));
  border-radius: 50px;
  position: absolute;
  top: 1px;
  left: 50%;
  transform: translateX(-50%);
}

.aqua-button:hover {
  transform: scale(1.04);
}

.aqua-button:active {
  background-color: #4f2525;
  border-color: #4f2525;
  transform: scale(.96);
}

